<template>
  <div class="hello">
    <el-form label-width="80px">
      <el-form-item label="Mnemonic">
        <el-input type="textarea" v-model="model.mnemonic"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button @click="exportModel" type="primary">导出wif+私钥</el-button>
        <el-button @click="reset">重置</el-button>
      </el-form-item>
      <el-form-item v-show="model.privateKeyWIF!=''" label="wif">
        <el-input readonly v-model="model.privateKeyWIF"></el-input>
      </el-form-item>
      <el-form-item v-show="model.privateKey!=''" label="私钥">
        <el-input readonly v-model="model.privateKey"></el-input>
      </el-form-item>
      <el-form-item v-show="model.address!=''" label="地址">
        <el-input readonly v-model="model.address"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  var bitcore = require('bitcore-lib');
  export default {
    name: 'HelloWorld',
    data() {
      return {
        model: {
          mnemonic: '',
          privateKeyWIF: '',
          privateKey: '',
          address: ''
        }
      }
    },
    methods: {
      exportModel() {

        var value = Buffer.from(this.model.mnemonic);
        let hash = bitcore.crypto.Hash.sha256(value);
        let bn = bitcore.crypto.BN.fromBuffer(hash);
        let privateKeyWIF = new bitcore.PrivateKey(bn).toString()
        let privateKey = bitcore.PrivateKey.fromWIF(privateKeyWIF);
        let address = privateKey.toAddress().toString();
        this.model.privateKey = privateKey.toString()
        this.model.privateKeyWIF = privateKey.toWIF()
        this.model.address = address
      },
      reset() {
        this.model = {
          mnemonic: '',
          privateKeyWIF: '',
          privateKey: '',
          address: ''
        }
      }
    },
    created() {
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .hello {
    width: 1280px;
    margin: 0 auto;
  }
</style>



